	module.exports = {"data":{"allPosts":{"edges":[{"node":{"id":"606261","title":"In brief: Hong Kong insurtech nabs $30m in series A funding led by Sun Life","excerpt":"Bowtie is also the first to receive a virtual insurance company license from the city’s insurance regulator.","featured_image":"https://cdn.techinasia.com/wp-content/uploads/2018/12/Bowtie.jpg","date_gmt":"2018-12-20T09:10:55","category":"brief","content":"<blockquote><p> <a href=\"https://www.bowtie.com.hk/en\" target=\"_blank\" rel=\"noopener\">Bowtie Life Insurance Company Limited (Bowtie)</a> announced that it has been granted the first virtual insurance company license by Hong Kong’s insurance authority under its “fast-track” pilot scheme. As a homegrown insurtech and a fully licensed virtual insurance company, Bowtie is set to offer insurance protection directly to customers, without any insurance intermediaries.</p></blockquote>\n<p>Source: Press release</p>\n<p>Bowtie has <a href=\"https://www.scmp.com/business/companies/article/2178887/home-grown-digital-insurer-bowtie-receives-hong-kongs-first\" target=\"_blank\" rel=\"noopener\">reportedly secured Hong Kong’s “largest series A funding of 2018</a>.” The virtual insurance startup raised US$30 million in a round led by insurance firm Sun Life Hong Kong. Also participating in the round was Hong Kong X Technology fund, a platform that counts Tencent’s Pony Ma and Sequoia China’s Neil Shen as backers.</p>\n<p>Bowtie will use the capital to enable new distribution modes through technology and digital innovations. It will introduce health insurance products in the first half of 2019, pending approvals from Hong Kong’s Food and Health Bureau.</p>\n<p><em>Currency converted from Hong Kong dollar. Rate: US$1 = HK$7.83</em></p>"}}]}},"pathContext":{"slug":"bowtie-30m-series-a"}}

